/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
  color: #25282a;
  font-family: "Proxima Nova Rg";
  font-size: 14px;
  line-height: 1.6;
}

blockquote {
  margin: 0 25px;
}

pre {
  max-width: 100%;
  padding: 1.6em;
  overflow: auto;
  background: #bebebe;
}

[class*='font-'] {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-size: 30px;
  line-height: 1;
}

/*--------------------------------------------------------------
## Base Layout
--------------------------------------------------------------*/
html, body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  height: 100%;
}

html {
  box-sizing: border-box;
}

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.wrapper {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 15px;
}

/*--------------------------------------------------------------
## Animations
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Parts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
.btn {
  display: inline-block;
  padding: 15px 36px;
  transition: 350ms ease;
  font-size: 13px;
  text-align: center;
  text-transform: uppercase;
}

.btn:active, .btn:focus {
  outline: none;
}

@media (max-width: 1024.98px) {
  .btn {
    padding: 7px 15px;
    font-size: 11px;
  }
}

.btn-contact {
  background-color: #f6b431;
  color: #fff;
  border: none;
  outline: none;
  width: 100%;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-contact:hover {
  background-color: #7cfc00;
  color: #fff;
}

.btn-intro {
  padding: 15px 40px;
  background-color: #344d71;
  color: #fff;
  max-width: 180px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-intro:hover {
  background-color: #4771af;
}

.btn-intro .icon {
  font-size: 8px;
  color: #fff;
  margin-left: 12px;
}

.btn-yellow {
  background-color: #f6b431;
  color: #fff;
}

.btn-yellow:hover {
  background-color: #ffd7b5;
}

/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Forms
--------------------------------------------------------------*/
input[type='text'],
input[type='tel'] {
  width: 100%;
  padding: 12px 33px;
  border-radius: 2px;
  outline: none;
  border: 1px solid #666;
  margin-bottom: 9px;
  transition: 350ms ease;
}

input[type='text']:hover,
input[type='tel']:hover {
  border-color: #f6b431;
}

@media screen and (max-width: 767px) {
  input[type='text'],
  input[type='tel'] {
    padding: 5px 33px;
  }
}

label {
  display: block;
  font-size: 13px;
  color: #d0ddf2;
  text-align: left;
}

/*--------------------------------------------------------------
## Wrapper and Ratios
--------------------------------------------------------------*/
img {
  max-width: 100%;
  display: block;
  height: initial;
}

