/*--------------------------------------------------------------
Menu-btn
--------------------------------------------------------------*/
.menu-btn {
  display: none;
  position: absolute;
  z-index: 110;
  top: 19px;
  right: 20px;
  width: 25px;
  height: 19px;
}

.menu-btn::after, .menu-btn::before,
.menu-btn span {
  display: block;
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #000;
}

.menu-btn::after, .menu-btn::before {
  content: '';
  transition: 350ms width 350ms ease;
}

.menu-btn::after {
  top: 0;
  left: 0;
}

.menu-btn::before {
  right: 0;
  bottom: 0;
  width: 25px;
}

.menu-btn span {
  top: 50%;
  margin-top: -1.5px;
  transition: transform 350ms ease;
}

.menu-btn span::before {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  transition: transform 350ms ease;
  background-color: #000;
}

.menu-btn.open::after, .menu-btn.open::before {
  width: 0;
  transition: 350ms ease;
}

.menu-btn.open::after {
  left: 0;
}

.menu-btn.open::before {
  right: 0;
}

.menu-btn.open span {
  transform: rotate(45deg);
  transition: 350ms transform 350ms ease;
}

.menu-btn.open span::before {
  transform: rotate(-90deg);
  transition: 350ms transform 350ms ease;
}

@media screen and (max-width: 1024px) {
  .menu-btn {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .menu-btn {
    top: 16px;
    display: block;
  }
}
